import React from 'react';
import {formatPrice} from '../helpers';
import CSSTransitionGroup from 'react-addons-css-transition-group' ;

class Order extends React.Component {

  constructor() {
    super();
    this.renderOrder = this.renderOrder.bind(this);

//     Use Arrow Function in Class Property
// This technique relies upon the proposed class property feature. To use this approach,
// you must enable transform-class-properties or enable stage-2 in Babel.
//    renderOrder= () => {};
  }

  renderOrder(key) {
    const fish = this.props.fishes[key];
    const count = this.props.order[key];
    const removeButton = <button onClick={()=> this.props.removeItem(key)}>&times;</button>;
    if (!fish || fish.status === 'unavailable') {
      return <li key={key}>Sorry, {fish ? fish.name: 'fish'} is no longer available</li>
    }
    return (
      <li key={key}>
          <span>
            <CSSTransitionGroup
              component="span"
              className="count"
              transitionName="count"
              transitionEnterTimeout={250}
              transitionLeaveTimeout={250}
            >
              <span key={count}>{count}</span>
            </CSSTransitionGroup>

            lbs {fish.name}
          </span>
          <span className='price'>{formatPrice(count * fish.price)}</span>
          {removeButton}
        </li>
      )
  }
  render() {
    const orderIds = Object.keys(this.props.order);
    const total = orderIds.reduce((prevTotal, key) => {
      const fish = this.props.fishes[key];
      const count = this.props.order[key];
      const isAvailable = fish && fish.status === "available";
      if (isAvailable) {
        return prevTotal + (count * fish.price || 0);
      }
      return prevTotal;
    }, 0);
    return (
      <div className="order-wrap">
        <h2>your order</h2>
        <CSSTransitionGroup
          className="order"
          component="ul"
          transitionName="order"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}
        >
          {orderIds.map(this.renderOrder)}
          <li className="total">
            <strong>Total:</strong>
            {formatPrice(total)}
          </li>
        </CSSTransitionGroup>
        {formatPrice(total)}
      </div >
    );
  }
}
Order.propTypes={
  fishes: React.PropTypes.object.isRequired,
  order: React.PropTypes.object.isRequired,
  removeItem: React.PropTypes.func.isRequired
}
export default Order;



